import React from 'react';
import { Link, Route } from 'react-router-dom';
import IndexWrapper from 'components/index/IndexWrapper';

const Header = () => {
  return (
    // <header className="header" onClick={onClick}>
    <header
      className="header fixed"
    >
    {/* 헤더 시작 */}
      <h1 className="header__logo">
        <Link
          to="/index"
          title="홈페이지 바로가기"
          className="index"
        >
          <img
            src={require("../../assets/img/img_logo.png")}
            alt="우푸 로고"
          />
        </Link>
      </h1>
      <nav className="header__gnb">
        <Link
          to="/subPromotion"
          title="첫 이용 할인"
          className="subPromotion"
        >첫 이용 할인
        </Link>
        <Link
          to="/subServices"
          title="서비스 소개"
          className="subServices"
        >
          서비스 소개
        </Link>
        <Link
          to="/subHelpP"
          title="고객센터"
          className="subHelpP"
        >
          고객센터
        </Link>
        <Link
          to="/subIncruit"
          title="우푸 버디 지원하기"
          className="subIncruit"
        >
          우푸 버디 지원하기
        </Link>
      </nav>
      {/* 헤더 끝 */}
    </header>
  );
};

export default Header;